
import index from '@/page/mobile/index.vue'
import blog from '@/page/mobile/blog.vue'
import music from '@/page/mobile/music.vue'
import resume from '@/page/mobile/resume.vue'

const mobileRouter = [
    {
        path: '/',
        component: index,
        redirect: '/web/index',
        children: [
            { path: '/web/index', name: 'mjia', component: index }
        ]
    },
    {
        path: '/web/blog',
        name: 'mjia',
        component: blog
    },
    {
        path: '/web/music',
        name: 'mjia',
        component: music
    },
    {
        path: '/resume',
        name: 'mjia',
        component: resume
    }
]
export default mobileRouter


